import Link from 'next/link'
import "../style.css"

const linkStyle = {
  marginRight: 15
}

const Header = () => (
  <div>
    <p className="title">Appart<span>Finder</span></p>
    <Link href="/">
      <a style={linkStyle}>Home</a>
    </Link>
    <Link href="/about">
      <a style={linkStyle} title="About Page">About</a>
    </Link>
  </div>
)

export default Header